@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&subset=latin&display=block');
/* @import url('https://fonts.googleapis.com/css2?family=IM+Fell+English:ital@0;1&subset=latin&display=block'); */

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
    font-family: 'Inter', ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    text-decoration-skip-ink: none;
}

@media screen and (min-width: 641px) {
    html {
        font-size: 12px;
    }
}

@media screen and (max-width: 640px) {
    html {
        font-size: 13px;
    }
}

@media screen and (max-width: 640px) {
    .nomobile {
        display: none;
    }
}

@media screen and (max-width: 640px) {
    .mobile-header {
        font-size: 300%;
    }
}

@font-face {
    font-family: 'fleurondingbats';
    src: url('fonts/fleurondingbats-webfont.woff2') format('woff2'),
         url('fonts/fleurondingbats-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'fleuronmixed';
    src: url('fonts/fleuronmixed-webfont.woff2') format('woff2'),
         url('fonts/fleuronmixed-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'fleuronregular';
    src: url('fonts/fleuronregular-webfont.woff2') format('woff2'),
         url('fonts/fleuronregular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.fleurondingbats {
    font-family: 'fleurondingbats', ui-serif, serif;
}
.fleuronmixed {
    font-family: 'fleuronmixed', ui-serif, serif;
}
.fleuronregular {
    font-family: 'fleuronregular', ui-serif, serif;
}

/* hack: for lucide icons*/
svg {
    display: inline;
}

.pixelated {
    image-rendering: pixelated;
}

ul li:before
{
    content: '❧';
    margin-right: 0.3em;

    /* from text-lg */
    font-size: 1.125rem; /* 18px */
    line-height: 1.75rem; /* 28px */
}

.project-header:before
{
    content: '❧';
    margin-right: 0.3em;

    /* from text-xl */
    font-size: 1.25rem; /* 20px */
    line-height: 1.75rem; /* 28px */
}
